.checkbox-dm{display:inline-flex;align-items:center;justify-content:center}.checkbox-dm__box{overflow:hidden;position:relative;white-space:nowrap;display:inline-block;max-width:18px;max-height:18px;cursor:pointer;margin:2px}.checkbox-dm__input{top:0;left:0;margin:0;padding:0;opacity:0;width:100%;z-index:10;height:100%;border:none;position:absolute}.checkbox-dm__check{width:18px;height:18px;background:var(--white);will-change:all;border-radius:4px;position:relative;display:inline-block;transition:all 80ms ease;box-shadow:inset 0 -1px 0 var(--black_10),inset 0 0 0 1px var(--black_15)}.checkbox-dm__check_checked::before{opacity:1}.checkbox-dm__check_indeterminate::after{opacity:1}.checkbox-dm__check::before,.checkbox-dm__check::after{inset:0;display:block;content:"";opacity:0;position:absolute}.checkbox-dm__check::before{background-image:url("data:image/svg+xml;charset=UTF-8,%3csvg width='13' height='10' viewBox='0 0 13 10' fill='none' xmlns='http://www.w3.org/2000/svg'%3e%3cpath d='M1.5 4.5L5 8L11 2' stroke='black' stroke-width='2' stroke-linecap='square'/%3e%3c/svg%3e");background-repeat:no-repeat;background-position:center;transition:all 120ms ease}.checkbox-dm__check::after{inset:4px;border-radius:2px}.checkbox-dm__label{width:100%;display:flex;align-items:center}.checkbox-dm:hover .checkbox-dm__check{background-color:var(--grape_100)}.checkbox-dm__input:checked+.checkbox-dm__check::before{opacity:1}.checkbox-dm_withLabel .checkbox-dm__box{margin-right:5px}