.userpic-dm{width:28px;height:28px;display:flex;font-size:12px;position:relative;align-items:center;border-radius:50%;justify-content:center;background:rgba(0,0,0,.08);box-shadow:0 0 0 1px var(--black_10)}.userpic-dm img{opacity:0;width:100%;height:100%;font-size:12px;line-height:22px;object-fit:cover;position:absolute;border-radius:100%}.userpic-dm__username{display:block;line-height:22px;font-weight:bold;opacity:.6}.userpic-dm__badge{position:absolute}.userpic-dm__badge_topLeft{top:0;left:0}.userpic-dm__badge_topRight{top:0;right:0}.userpic-dm__badge_bottomLeft{bottom:0;left:0}.userpic-dm__badge_bottomRight{bottom:0;right:0}.userpic-dm_faded{background:rgba(0,0,0,.03);box-shadow:inset 0 0 0 1px var(--black_10)}.userpic-dm_faded .userpic-dm__username{opacity:.2}.userpic-dm_faded img{opacity:.3}