.menu-dm{flex:1;margin:0;padding:8px;display:flex;flex-direction:column;list-style-type:none}.menu-dm ul,.menu-dm li{display:block;list-style-type:none}.menu-dm li{margin:.5rem}.menu-dm li+li{margin-top:0}.menu-dm__item{height:32px;display:flex;cursor:pointer;padding:0 12px;border-radius:3px;align-items:center;box-sizing:border-box;color:var(--sand_600);font-size:14px;white-space:nowrap;user-select:none}.menu-dm__item-icon{margin-right:10px;object-fit:contain;opacity:.5;display:flex;align-items:center;justify-content:center}.menu-dm__item:not(.menu-dm__item_look_danger):hover{color:var(--grape_700);background-color:var(--grape_0)}.menu-dm__item_active{color:var(--grape_700);background-color:var(--grape_0)}.menu-dm__item_active:not(.sidebar__pin,.menu-dm__item_clickable){pointer-events:none}.menu-dm__item:hover .menu-dm__item-icon,.menu-dm__item_active .menu-dm__item-icon{opacity:1}.menu-dm__item_look_danger{color:var(--danger_color)}.menu-dm__item_look_danger:hover{background-color:var(--red_1)}.menu-dm__spacer{flex:1}.menu-dm__divider{height:1px;margin:8px 0;background-color:var(--black_10)}.menu-dm_size_compact .menu-dm__item{height:32px;font-size:16px}.menu-dm_size_small .menu-dm__item{height:24px;font-size:14px;padding:0 10px}.menu-dm_collapsed{padding:0}.menu-group-dm__title{padding:4px 10px;font-size:14px;color:rgb(var(--black-raw)/60%)}.menu-group-dm__list{padding:0;margin-left:10px;list-style-type:none}