.select-dm{height:40px;background:#fafafa;font-size:16px;line-height:22px;border:1px solid var(--sand_300);box-sizing:border-box;border-radius:5px;cursor:pointer}.select-dm_size_compact{height:32px}.select-dm_size_small{height:24px;font-size:12px}.select-dm__list{width:max-content}.select-dm__selected{width:100%;padding:0 7px;min-width:60px;display:flex;font-weight:500;align-items:center}.select-dm__value{width:max-content;min-width:30px;flex:1;white-space:nowrap;overflow:hidden;text-overflow:ellipsis}.select-dm__option{cursor:pointer;padding:4px 16px}.select-dm__option:hover,.select-dm__option_focused{background-color:#f7f7f7}.select-dm__option_selected{color:#fff;background-color:var(--accent_color)}.select-dm__option_selected:hover,.select-dm__option_selected.select-dm__option_focused{background-color:hsl(var(--accent_color), 10%)}.select-dm__icon{width:16px;height:16px;margin-left:5px;position:relative}.select-dm__icon::before,.select-dm__icon::after{width:6px;height:6px;display:block;position:absolute;content:"";border:1px solid var(--accent_color);border-right:none;border-bottom:none}.select-dm__icon::before{top:0;left:50%;transform:translate(-50%, 50%) rotate(45deg)}.select-dm__icon::after{bottom:0;left:50%;transform:translate(-50%, -50%) rotate(-135deg)}.select-dm__optgroup-list{width:max-content}.select-dm__optgroup-label{padding:4px 16px;color:rgb(var(--black-raw)/60%)}.select-dm__optgroup-list .select-dm__option{padding-left:24px}.select-dm_disabled{pointer-events:none}.select-dm:focus{outline:none;box-shadow:0 0 0 6px rgb(var(--accent_color-raw)/20%),inset 0 -1px 0 var(--black_10),inset 0 0 0 1px var(--black_15),inset 0 0 0 1px rgb(var(--accent_color-raw)/20%);border-color:rgb(var(--accent_color-raw)/20%)}